<template>
	<div class="content_t">
		<div class="fixed">
			<van-nav-bar title="当前城市 -" left-text="asdsad" :left-arrow="true">
				<template #left>
					<van-icon name="cross" size="24" color="black" @click="$router.back()" />
				</template>
			</van-nav-bar>
			<van-search v-model="value" placeholder="输入城市名或拼音" :clearable="focus" :show-action="focus"
				@focus="focus=true" @cancel="focus=false" />
		</div>
		<div class="value">
			<div v-show="value===''">
				<div class="plan" v-if="isRender">
					<div class="gps">
						<p>GPS定位你所在城市</p>
						<div>
							<span v-if="!getCity">{{'定位失败'}}</span>
							<span @click="chooseCity(isLocation)" v-else>{{isLocation.name}}</span>
						</div>
					</div>
					<div class="hot">
						<p>热门城市</p>
						<ul>
							<li v-for="value in hotCity" @click="chooseCity(value)">{{value.name}}</li>
						</ul>
					</div>
				</div>
				<van-index-bar :sticky="false" :index-list="!value?indexList:[]">
					<div>
						<div v-for="(v,i) in cityTree">
							<van-index-anchor :index="i" />
							<van-cell v-for="value in v" :title="value.name" @click="chooseCity(value)" />
						</div>
					</div>
				</van-index-bar>
			</div>
			<div v-show="value!=''" class="search_value">
				<van-index-bar :sticky="false" :index-list="!value?indexList:[]">
					<div>
						<div v-for="(v,i) in searchCity">
							<van-cell class="my" :title="v.name" @click="chooseCity(v)" />
						</div>
					</div>
				</van-index-bar>
			</div>
		</div>
	</div>
</template>

<script>
	import Vue from 'vue';
	import axios from "axios"
	import {
		Toast
	} from 'vant';
	Vue.use(Toast);
	export default {
		data() {
			return {
				value: "",
				focus: false,
				getCity: true,
				allCityList: [],
				cityTree: {},
				isRender: false,
				isLocation: "",
				hotCity: [],
				searchCity: [],
				timer: [],
				indexList: [],
			}
		},
		created() {
			this.getCityId()
		},
		methods: {
			city() { //定义获取城市方法
				let this_ = this
				try {
					let geolocation = new BMap.Geolocation();
					geolocation.getCurrentPosition(function getinfo(position) {
						let city = position.address.city; //获取城市信息
						// let province = position.address.province; //获取省份信息
						this_.getCity = true;
						this_.isNowCity(city);
						return city
					}, function(e) {
						this_.getCity = false
					}, {
						provider: 'baidu'
					});
				} catch (e) {
					this_.getCity = false
				}
			},
			axios_() {
				return axios({
						url: "https://m.maizuo.com/gateway?k=7102382",
						headers: {
							'X-Client-Info': '{"a":"3000","ch":"1002","v":"5.0.4","e":"16352477313746499972300801","bc":"110100"}',
							'X-Host': 'mall.film-ticket.city.list'
						}
					})
					.then(value => {
						this.allCityList = value.data.data.cities;
						value.data.data.cities.some(val => val === value)
					})
			},
			async getCityId() {
				Toast.loading({
					message: '定位中...',
					forbidClick: true,
					duration: 0
				});
				await this.axios_()
				await this.city()
				Toast.clear();
			},
			chooseCity(value) {
				//存状态，跳转
				/**
				 * 等待vuex
				 */
				this.$store.commit("changeCity", value)
				this.$router.back()
			},
			search(val) {
				let pattern2 = new RegExp("[A-Za-z]+")
				this.searchCity = []
				clearTimeout(this.timer[0])
				let arr1 = [];
				let arr2 = [];
				/**
				 * 匹配模式：模糊搜索，
				 * 1.全称匹配，仅返回匹配的城市
				 * 2.首字母匹配和包含匹配，将首字母匹配放在最前，包含置后
				 * 3.多字母匹配，综合匹配+分解匹配
				 * 节流
				 */
				this.timer[0] = setTimeout(() => {
					try {
						this.allCityList.forEach(v => {
							if (pattern2.test(val)) {
								let str = v.pinyin
								if (str === val) {
									arr1.push(v)
									throw new Error("停止")
								}
								if (str.startsWith(val)) {
									arr1.push(v)
								} else {
									if (str.includes(val, 1)) {
										arr2.push(v)
									} else {
										let is = []
										let is2 = []
										let start = 0
										if (val.length != 1) {
											for (let i = 0; i < val.length; i++) {
												if (str.startsWith(val[0])) {
													if (str.includes(val[i], start + 1) && i !== 0) {
														start = str.indexOf(val[i])
														is2.push(true)
													}
												} else {
													if (str.includes(val[i], start + 1)) {
														start = str.indexOf(val[i])
														is.push(true)
													}
												}
											}
											if (is2.length === val.length - 1) {
												arr1.push(v)
											}
											if (is.length === val.length) {
												arr2.push(v)
											}
										}
									}
								}
							} else {
								if (v.name.includes(val)) {
									arr1.push(v)
								}
							}
						})
					} catch (e) {
						//TODO handle the exception
					}
					this.searchCity = [...arr1, ...arr2]
				}, 200)
			},
			isNowCity(value) {
				try {
					this.allCityList.forEach(v => {
						if (v.name === value.slice(0, value.length - 1)) {
							this.isLocation = v
							throw new Error(0);
						}
					})
				} catch (e) {

				}
			}
		},
		watch: {
			allCityList() {
				//创建城市字典的索引
				for (var i = 65; i <= 90; i++) {
					this.cityTree[String.fromCharCode(i)] = []
				}
				//循环citylist并将拼音首字母进行映射到字典
				this.allCityList.forEach(val => {
					this.cityTree[val.pinyin.toUpperCase().slice(0, 1)].push(val)
					//将热门城市存到数组
					val.isHot === 1 ? this.hotCity.push(val) : ''
				})
				//过滤掉空的属性
				for (let i in this.cityTree) {
					if (this.cityTree[i].length === 0) {
						delete this.cityTree[i]
					} else {
						//添加索引
						this.indexList.push(i);
					}
				}

				this.isRender = true;
			},
			//搜索算法
			value(val) {
				this.search(val)
			},
		},
		// directives: {
		// 	"check": {
		// 		inserted(el) {
		// 			console.log(el.innerText)
		// 			if (el.innerText === "定位失败") {
		// 				el.setAttribute("disabled", false)
		// 			} else {
		// 				el.removeAttribute("checked")
		// 			}
		// 		}
		// 	}
		// }
	}
</script>

<style lang="scss">
	html,
	body {
		height: 100%;
		background-color: #fff;
	}

	.content_t {

		.plan {
			width: 100%;
			background-color: #fff;
			padding: 0.9375rem;
			box-sizing: border-box;

			.search_value {
				position: absolute;
				z-index: 4;
				background-color: #fff;

				.my {
					border-bottom: 1px solid;
				}
			}

			.gps {
				p {
					font-size: 0.8125rem;
					color: #797d82;
					margin-bottom: 0.625rem;
				}

				div {
					width: 100%;
					height: 2.8125rem;
					display: flex;
					margin: 0 0.46875rem;

					span {
						width: calc((100% - 60px)/3);
						color: #191a1b;
						background-color: #f4f4f4;
						height: 1.875rem;
						font-size: 0.875rem;
						border-radius: 3px;
						display: flex;
						align-items: center;
						justify-content: center;
					}
				}
			}

			.hot {
				p {
					font-size: 0.8125rem;
					color: #797d82;
					margin-bottom: 0.625rem;
				}

				ul {
					display: flex;
					flex-wrap: wrap;

					li {
						width: calc((100% - 60px)/3);
						color: #191a1b;
						background-color: #f4f4f4;
						height: 1.875rem;
						font-size: 0.875rem;
						border-radius: 3px;
						display: flex;
						align-items: center;
						justify-content: center;
						margin: 0 0.46875rem;
						margin-bottom: 0.9375rem;
					}
				}
			}
		}

		// *****************************************
		.van-nav-bar__content {
			height: 2.75rem;

			.van-nav-bar__left,
			.van-nav-bar__right {
				padding: 0 8px;
			}
		}


		.fixed {
			width: 100%;
			position: fixed;
			top: 0;
			border-bottom: 1px solid #fff;
		}

		.value {
			width: 100%;
			height: calc(100% - 93px);
			position: fixed;
			top: 93px;
			overflow: auto;
		}

		.value::-webkit-scrollbar {
			width: 0;
		}


		.van-search {
			height: 49px;
			background-color: #f4f4f4;
			padding: 9.5px 15px 9.5px 15px;
		}

		.van-search__content {
			background-color: #fff;
			border-radius: 4px;
			height: 1.875rem;

			.van-field__control {
				font-size: 12px;
			}
		}

		.van-index-bar {
			.van-index-bar__sidebar {
				bottom: 80px;
				top: auto;
				transform: translateY(0);

				.van-index-bar__index {
					padding: 2px 2px 0 10px;
				}

			}
		}

		.van-index-anchor {
			padding-right: 0;
			margin-right: 16px;
			background-color: #f4f4f4;
		}

		.van-cell__title::after {
			position: absolute;
			box-sizing: border-box;
			content: ' ';
			pointer-events: none;
			right: 16px;
			bottom: 0;
			left: 16px;
			border-bottom: 1px solid #ebedf0;
			-webkit-transform: scaleY(.5);
			transform: scaleY(.5);
		}



	}
</style>
